.wp-block[data-type="inblocks/image"] .dashicons-format-image {
	color: #00b093;
}

button[id$="block-inblocks/image"] {
	color: #00b093;
}

.editor-block-list-item-inblocks-image .dashicons-format-image {
	color: #00b093;
}

.wp-block-inblocks-image .inblocks-insert-wrap {
	-webkit-box-align: center;
	-webkit-align-items: center;
	-ms-flex-align: center;
	align-items: center;
	background-color: rgba(255, 255, 255, 0.95);
	bottom: 0;
	cursor: -webkit-zoom-out;
	cursor: zoom-out;
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	-webkit-justify-content: center;
	-ms-flex-pack: center;
	justify-content: center;
	position: absolute;
	right: 0;
	top: 0;
	width: 100%;
	z-index: 99;
}

.wp-block-inblocks-image .inblocks-insert-wrap .inblocks-insert-url-input {
	height: 36px;
	width: 75%;
}

.wp-block-inblocks-image iframe {
	border-color: #e0e1e3;
	border-style: solid;
	display: inline-block;
	max-width: 100%;
	padding: 1px;
	position: relative;
}

.wp-block-inblocks-image .dashicons {
	font-size: 24px;
	margin-right: 12px;
	position: relative;
	top: 0;
}

.wp-block-inblocks-image .inblocks-instagram-media {
	text-align: center;
}

.wp-block-inblocks-image .inblocks-media-wrap {
	position: relative;
	width: 100%;
	margin-bottom: 20px;
}

.wp-block-inblocks-image .inblocks-media-wrap p {
	font-size: 14px;
	padding-bottom: 10px;
}

.wp-block-inblocks-image .inblocks-media-wrap .inblocks-preview-strip {
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	overflow-x: scroll;
	height: 210px;
}

.wp-block-inblocks-image .inblocks-media-wrap .inblocks-preview-strip::-webkit-scrollbar {
	border-radius: 12px;
	cursor: pointer;
	height: 12px;
	width: 12px;
}

.wp-block-inblocks-image .inblocks-media-wrap .inblocks-preview-strip::-webkit-scrollbar-track-piece {
	background-color: #e0e1e3;
	border-radius: 12px;
}

.wp-block-inblocks-image .inblocks-media-wrap .inblocks-preview-strip::-webkit-scrollbar-thumb:horizontal {
	background-color: #2271b1;
	border-radius: 12px;
	cursor: pointer;
	height: 30px;
}

.wp-block-inblocks-image .inblocks-media-wrap .inblocks-preview-strip img {
	height: 180px;
	max-width: unset;
	outline: 10px solid #ffffff;
	cursor: pointer;
}

.inblocks-panel-row {
	padding: 15px 0 20px 0;
}

.inblocks-button-next-url {
	display: block;
	margin-top: 10px;
}

